/* jshint ignore:start */

define('admissions-game/config/environment', ['ember'], function(Ember) {
  var prefix = 'admissions-game';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("admissions-game/tests/test-helper");
} else {
  require("admissions-game/app")["default"].create({"name":"admissions-game","version":"0.0.0+50c8898e"});
}

/* jshint ignore:end */
